.gallery {
  padding-top: 60px;
}

.trending,
.library {
  display: grid;
  justify-content: center;

  @media screen and (min-width: $min-mobile) and (max-width: $max-mobile) {
    grid-template-columns: 280px;
    grid-row-gap: 30px;
  }
  @media screen and (min-width: $min-tablet) and (max-width: $max-tablet) {
    grid-template-columns: 293px 293px;
    grid-column-gap: 30px;
    grid-row-gap: 30px;
  }
  @media screen and (min-width: $min-desktop) {
    grid-template-columns: 274px 274px 274px;
    grid-column-gap: 30px;
    grid-row-gap: 30px;
  }
}

.gallery-item {
  position: relative;
  border-radius: 5px;
  cursor: pointer;
  transition: transform $time $cubic;
}
body.dark-theme .gallery-item:hover,
body.dark-theme .gallery-item:focus {
  transform: scale(1.05);
  box-shadow: 0px 0px 15px -2px $team-thumb-shadow-light;
}

.gallery-item:hover,
.gallery-item:focus {
  transform: scale(1.05);
  box-shadow: 0px 0px 15px -2px $team-thumb-shadow-dark;
}

.movie__thumb {
  position: relative;
}
.movie__description {
  padding: 2px;
}

.movie__image {
  margin-bottom: 10px;
  border-radius: 5px;

  @media screen and (min-width: $min-mobile) and (max-width: $max-mobile) {
    width: 280px;
    height: 402px;
  }
  @media screen and (min-width: $min-tablet) and (max-width: $max-tablet) {
    width: 293px;
    height: 398px;
  }
  @media screen and (min-width: $min-desktop) {
    width: 274px;
    height: 398px;
  }
}

.movie__name {
  display: block;
  text-transform: uppercase;
  font-weight: 500;
}

.movie__genres {
  display: inline-block;
  font: $main-font;
  color: $accent-color;
  font-weight: 500;
  font-size: $main-fontsize;
  line-height: 1.3;
}

.movie__release {
  display: inline-block;
  font: $main-font;
  color: $accent-color;
  font-weight: 500;
  font-size: $main-fontsize;
  line-height: 1.33;
}

.movie__vote-average {
  position: absolute;
  bottom: 0;
  right: 0;

  display: flex;
  align-items: center;
  justify-content: center;

  width: 38px;
  height: 18px;
  font-family: $main-font;
  font-weight: 500;
  font-size: 13px;
  line-height: 1.1;
  color: $white;
  background-color: $accent-color;
  border: 1px solid $dark-grey;
  border-radius: 5px;

  pointer-events: none;
}

body.dark-theme .movie__vote-average {
  color: $accent-color;
  background-color: $dark-grey;
  border: 1px solid $accent-color;
}
